@import url(https://fonts.googleapis.com/css?family=Old+Standard+TT|Satisfy|Special+Elite&display=swap);
body {
  margin: 0;
  font-family: "Old Standard TT", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
}

.App-header h3,
.App-header div {
  cursor: pointer;
}

.App-link {
  color: #09d3ac;
}

